body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --input-padding-x: 1.5rem;
  --input-padding-y: .75rem;
}

body {
  background: #E9EBEE;
}

.card-title {
  padding-top: 15px;
  color: violet;
  margin-bottom: 0px !important;
}

.card-signin {
  border: 0;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
}

.card-signin .card-title {
  margin-bottom: 2rem;
  font-weight: 300;
  font-size: 1.5rem;
}

.card-signin .card-body {
  padding: 2rem;
}

i {
  vertical-align: middle;
}

.table-users tbody tr:hover {
  cursor: pointer;
  background-color: #eee;
}

.nav-user-photo {
  vertical-align: middle;
}

.user_panel {
  width: 50%;
}
nav>.nav.nav-tabs {
  border: none;
  color: #fff;
  background: #272e38;
  border-radius: 0;
}

nav>div button.nav-item.nav-link, nav>div button.nav-item.nav-link.active {
  border: none;
  padding: 18px 25px;
  color: #fff;
  background: #272e38;
  border-radius: 0;
  border-right: 1px solid #fff;
  cursor: pointer;
}

nav>div button.nav-item.nav-link.active:after {
  content: "";
  position: relative;
  bottom: -60px;
  left: -10%;
  border: 15px solid transparent;
  border-top-color: #e74c3c;
}

nav>div button.nav-item.nav-link:hover, nav>div button.nav-item.nav-link:focus {
  border: none;
  background: #e74c3c;
  color: #fff;
  border-radius: 0;
  transition: background 0.20s linear;
}
.listing-block {
    padding-top: 20px;
    overflow-y: scroll;
    max-height: calc(100vh - 70px);
}

.media {
    position: relative;
    margin-bottom: 15px;
    width: 100%;
    cursor: pointer;
}

.media img {
    width: 200px;
    margin: 0;
    height: 136px;
}

.media-body {
    border: 1px solid #bcbcbc;
    border-left: 0;
    height: 136px;
}

.media .name {
    float: left;
    width: 100%;
    font-size: 30px;
    font-weight: 600;
    color: #4765AB;
}

.media .mutual {
    float: left;
    width: 100%;
    margin-top: 10px;
}

.media .mutual span {
    float: left;
    margin-right: 10px;
    font-size: 15px;
}

.media .mutual span i {
    margin-right: 7px;
    color: #4765AB;
}

.media .address {
    float: left;
    width: 100%;
    font-size: 14px;
    margin-top: 5px;
    color: #888;
}
.card-img-top{
    max-height: 600px;
}

.thread-item{
    margin-bottom: 30px;
}
.me {
    font-size: 14px;
    border-right: 3px solid #60DF88 !important;
    padding: 5px;
    background-color: rgb(239, 255, 241);
    margin-right: 10px;
}

.you {
    font-size: 14px;
    border-left: 3px solid rgb(255, 112, 0) !important;
    padding: 5px;
    padding-left: 10px;
    background-color: rgb(247, 247, 214);
}

.left_btn {
    border-radius: 15px 0 0 15px !important;
    background-color: rgba(0, 0, 0, 0.3) !important;
    border: 0 !important;
    color: white !important;
    cursor: pointer;
}

.reload_btn {
    border-radius: 0 15px 15px 0 !important;
    background-color: rgba(0, 0, 0, 0.9) !important;
    border: 0 !important;
    color: white !important;
    cursor: pointer;
}

.type_msg {
    background-color: rgba(0, 0, 0, 0.3) !important;
    border: 0 !important;
    color: white !important;
    height: 60px !important;
    overflow-y: auto;
}

.type_msg:focus {
    box-shadow: none !important;
    outline: 0px !important;
}

.panel {
    margin-bottom: 0;
    height: calc(100vh - 70px);
    position: relative;
}

.panel-title {
    text-align: center;
    font-size: 20px;
    background: #60DF88;
    cursor: default;
    
}

.panel-body {
    overflow: auto;
    height: calc(100% - 140px);
    margin-top: 30px;
    display: flex; 
    flex-direction: column; 
}

.message {
    vertical-align: bottom;
}

.panel-footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
}
